export function fun_send(methodName, params, callback) {
  axios({
    headers: {
      "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
    },
    method: "post",
    url: "/api/" + methodName,
    data: params,
    dataType: "json"
  })
    .then(result => {
      callback(result.data);
      // window.console.log( this.baseData );
    })
    .catch(err => {
      window.console.log(err);
    });
}

export function fun_getCars(params) {
  var p = new URLSearchParams(params);
  return axios.post("/api/GetCars", p);
}

export function fun_getDays(params) {
  var p = new URLSearchParams(params);
  return axios.post("/api/GetDays", p);
}

export function fun_makenode(input, isopen) {
  var data = [];
  for (var i = 0; i < input.length; i++) {
    if (isopen == true) {
      data.push({
        title: input[i],
        loading: false,
        children: []
      });
    } else {
      data.push({
        title: input[i]
      });
    }
  }
  return data;
}
