<style scoped>
#title {
    font-size: 60px;
}
</style>

<template>
    <div>
        <div id="title">主页</div>
        <i-button @click="changename">test</i-button>
        <div style="width: 30px"></div>
        <i-tree :data="baseData" :load-data="loadData"></i-tree>
    </div>
</template>


<script>
import axios from "axios";

import { fun_getCars, fun_getDays, fun_makenode } from "@/assets/js/postmethods"

export default {
    name: "MainPage",

    data() {
        return {
            baseData: [
                {
                    title: "车辆",
                    loading: false,
                    children: []
                }
            ]
        };
    },
    methods: {
         changename() {

              
            
            this.$store.dispatch("changeUserName", "123");
        },
        async loadData(item, callback) {
            if (item.title == "车辆") {
                var params = {username:'23'}

                var {data:result} = await fun_getCars(params)
                window.console.log(result)
               setTimeout(() => {
                         callback(fun_makenode(result, true))
                    }, 200)

                return 
            } else {
                if (/^[0-9ABCDEF]{8}$/.test(item.title)) {
                    var params = { carid: item.title}
                    var {data:result} = await fun_getDays(params)

                    setTimeout(() => {
                         callback(fun_makenode(result, true))
                    }, 200)
                    
                }
                else if (/^[0-9]{4}-[0-9]{2}-[0-9]{2}$/.test(item.title)) {
                   
                    
                    setTimeout(() => {
                       callback( [{title:'123'}] )
                    }, 300);
                }
            }
        }
    },
    mounted() {
        return;
    }
};
</script>

